import React, {useEffect, useState} from 'react';
import styles from './pages.module.css'
import cn from "classnames";
import {Button, EmailInput, Input, PasswordInput} from "@ya.praktikum/react-developer-burger-ui-components";
import {Link, useNavigate} from "react-router-dom";
import {registerUserRequest} from "../services/slice/userSlice";
import {useAppDispatch, useAppSelector} from "../services/hooks";
import {IInputRegisterUpdate} from "../services/types";

const RegisterPage = () => {
  const dispatch = useAppDispatch();
  const userData = useAppSelector(state => state.userSlice)
  const [user, setUser] = useState<IInputRegisterUpdate>({name: '', email: '', password: ''} );
  const navigate = useNavigate();
  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const {name, value} = e.target
    setUser({
      ...user,
      [name]: value
    })
  }
  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const {name, password, email} = user;
    if(!name || !password || !email)
      return;
    dispatch(registerUserRequest({name, email, password}));
  }
  useEffect(()=>{
    if(userData.isUserLoaded) {
      navigate('/')
    }
  }, [userData])

  return (
    <form className={cn(styles.form_wrapper)} onSubmit={handleSubmit}>
      <h2 className="text text_type_main-medium"></h2>
      <Input
        type={'text'}
        placeholder={''}
        onChange={onChange}
        value={user.name || ''}
        name={'name'}
        error={false}
        errorText={''}
        size={'default'}
        extraClass="mt-6"
      />
      <EmailInput
        value={user.email || ''}
        onChange={onChange}
        name={'email'}
        placeholder="E-mail"
        isIcon={false}
        extraClass="mt-6"
      />
      <PasswordInput
        value={user.password || ''}
        onChange={onChange}
        name={'password'}
        placeholder=""
        extraClass="mt-6"
      />
      <Button htmlType="submit" type="primary" size="large" extraClass="mt-6 mb-20">
        
      </Button>
      <div className={cn(styles.login_redirect, 'mb-4')}>
        <p className="text text_type_main-default text_color_inactive"> ?</p>
        <Link to={"/login"}>
          <Button htmlType="button" type="secondary" size="medium" extraClass='pl-2 ' style={{paddingTop: 0, paddingBottom: 0, paddingRight: 0 }}>
            
          </Button>
        </Link>
      </div>
    </form>
  );
};

export default RegisterPage;